/**
 * Problem (Model)
 * Modelo de problema.
 * @export
 * @class Problem
 */
export class Problem {
  /**
   * Crea una instancia de Problem.
   * @param {number} id - Identificador del problema
   * @param {string} titleEN - Ttulo del problema en ingles
   * @param {string} titleES - Titulo del problema en espaol
   * @param {number} level - Nivel del problema
   * @param {number} category - Identificador de la categora
   * @param {string} categoryName - Nombre de la categora
   * @param {string} descriptionEN - Enunciado del problema en ingls
   * @param {string} descriptionES - Enunciado del problema en espaol
   * @param {string} exampleIput - Entradas de ejemplo
   * @param {string} exampleOutput - Salidas de ejemplo
   */
  constructor (id = undefined, titleEN = undefined, titleES = undefined, level = undefined, category = undefined, categoryName = undefined, descriptionEN = undefined, descriptionES = undefined, exampleInput = undefined, exampleOutput = undefined, timeLimit = undefined, input = undefined, output = undefined, author = undefined, authorName = undefined) {
    this.id = id
    this.titleEN = titleEN
    this.titleES = titleES
    this.level = level
    this.category = category
    this.categoryName = categoryName
    this.descriptionEN = descriptionEN
    this.descriptionES = descriptionES
    this.exampleInput = exampleInput
    this.exampleOutput = exampleOutput
    this.timeLimit = timeLimit
    this.input = input
    this.output = output
    this.author = author
    this.authorName = authorName
  }

  /**
   * Indica si el problema tiene versin en ingls.
   * @return true si el problema est en ingles, false en caso contrario
   */
  isInEnglish () {
    return this.titleEN != null
  }

  /**
   * Indica si el problema tiene versin en espaol.
   * @return true si el problema est en espaol, false en caso contrario
   */
  isInSpanish () {
    return this.titleES != null
  }
}
